import { Component, Input, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { NgIf, NgTemplateOutlet, NgClass, NgSwitch, NgSwitchCase, NgFor } from '@angular/common';
import { Subject } from 'rxjs';
import { MatIconModule } from '@angular/material/icon';
import { coerceBooleanProperty } from '@angular/cdk/coercion';

@Component({
  selector: 'dsc-footer',
  templateUrl: './dsc-footer.component.html',
  styleUrls: ['./dsc-footer.component.scss'],
  standalone: true,
  imports: [NgTemplateOutlet, NgIf, MatIconModule, NgClass, NgSwitch, NgSwitchCase, NgFor],
  encapsulation: ViewEncapsulation.None
})
export class DscFooterComponent implements OnDestroy {
  @Input() theme: 'highlight' | 'neutral' = 'highlight';
  @Input() model: 'model1' | 'model2' | 'model3' | 'model4' = 'model1';
  @Input() showLogo: boolean = true;
  @Input() showCGC: boolean = true;
  @Input() showSigla: boolean = true;
  @Input() showAdditionalText: boolean = true;
  @Input() showVersion: boolean = true;
  @Input() version: string = '';
  
  @Input() cgc: string = '';
  @Input() sigla: string = '';
  @Input() additionalText: string = '';
  @Input() sectionTitleText: string = '';
  @Input() sectionText: string = '';
  @Input() internalLinkTitle: string = '';
  @Input() externalLinkTitle: string = '';
  @Input() internalLinks: { text: string, url: string }[] = [];
  @Input() externalLinks: { text: string, url: string }[] = [];
  @Input() columns: { label: string; text: string }[] = [];
  @Input()
    get brandFull(): boolean {
      return this._brandFull;
    }
  
    set brandFull(value: boolean | string) {
      this._brandFull = coerceBooleanProperty(value);
    }
  
    private _brandFull = true;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  isNotScreenSmall: boolean = true;


  ngOnDestroy(): void {
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }
}
